import React, { useState, useEffect } from "react";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import { Table } from "react-bootstrap";

function Currency() {
  let [curr, setCurr] = useState([]);
  let [pic, setPic] = useState([]);
  useEffect(() => {
    async function fetchData() {
      const result = await axios.get("/api/getCurrency");
      //"/api/getCurrency"
      //`https://my-own-cors-proxy-651654.herokuapp.com/https://poe.ninja/api/data/CurrencyOverview?league=Scourge&type=Currency`
      //`api/data/CurrencyOverview?league=Scourge&type=Currency`
      //console.log(result.data.lines[0]);
      console.log(result);
      setCurr(result.data); //server
      //setCurr(result.data.lines); //dev
      let tmpArray = [];
      for (let item of result.data.lines) {
        for (let detail of result.data.currencyDetails) {
          if (item.currencyTypeName === detail.name) {
            //console.log(detail.name);
            tmpArray.push(detail.icon);
          }
        }
      }
      setPic([...tmpArray]);
    }
    fetchData();
  }, []);

  async function fetchPic(id) {
    console.log("inside fetchPic, fetching " + pic[id]);
    const result = await axios.get("/api/getPicture/" + pic[id]);
    return result.data;
  }
  let listItems = curr.map((cur, id) => (
    <tr key={id}>
      <td>
        <img
          src={(id) => {
            fetchPic(id);
          }}
          alt={"Picture of " + cur.currencyTypeName}
        ></img>{" "}
        {cur.currencyTypeName}
      </td>
      <td>{cur.chaosEquivalent}</td>
    </tr>
  ));
  return (
    <Table>
      <thead>
        <tr>
          <th>Item</th>
          <th>Price</th>
        </tr>
      </thead>
      <tbody>{listItems}</tbody>
    </Table>
  );
}

export default Currency;
